import React from 'react'
import './psyContent.css'
import propsTypes from 'prop-types'
export default class PsyContent extends React.Component{
    
    propsTypes={
        psyData:propsTypes.shape({
            _id:propsTypes.string,
            image:propsTypes.string,
            title:propsTypes.string,
            des:propsTypes.string
        }).isRequired
        
    }
    render(){
        let {image,title,des}=this.props.psyData
        return(
            <div className="psyContent" >
                <img src={image} alt=""/>
                <div className="psyWord">
                    <h3>{title}</h3>
                    <p>{des}</p>
                </div>
            </div>
           
        )
    }
}