import React from 'react'
import {withRouter} from 'react-router-dom'
import './findContent.css'
import propsTypes from "prop-types"
class FindContent extends React.Component{
    static defaultProps={
        index:undefined,
        to:null
    }
    static propsTypes={
        findData:propsTypes.shape({
            _id:propsTypes.string.isRequired,
            title:propsTypes.string,
            des:propsTypes.string,
            icon:propsTypes.string,
            author:propsTypes.string,
            time:propsTypes.string,
            read:propsTypes.string,
            comments:propsTypes.string,
            like:propsTypes.string,
            iconImg:propsTypes.string
        }).isRequired,
        to:propsTypes.shape({
            apiname:propsTypes.string.isRequired,
            pathname:propsTypes.string.isRequired
        })
    }
    to=(_id)=>{
        if(!this.props.to) return;
        let {history,to:{pathname,apiname}}=this.props;
        history.push({pathname:`${pathname}/${_id}`,search:`apiname=${apiname}`})
    }
    render(){
        let {findData}=this.props
        return(
            <div className="findContent" onClick={()=>this.to(findData._id)}>
               <div className="findFlex">
                    <div className="findLeft">
                        <h3>{findData.title}</h3>
                        <p>{findData.des}</p>
                    </div>
                    <div className="findRight">
                        <img src={findData.iconImg}/>
                    </div>  
               </div>
                <a className="find_a"><img src={findData.detail.auth_icon}/><span>{findData.detail.auth} {findData.time} {findData.num}阅读 {findData.num}评论 {findData.num}喜欢</span></a>
            </div>
        )
    }
}
export default withRouter(FindContent)