import React from 'react'
import './cell.css'
import propsTypes from 'prop-types'
import arrow from '../../assets/img/arrow.png'
export default class Cell extends React.Component{
    static defaultProps={
        clickHand:()=>{},
        style:{}
    }
    static propsTypes={
        clickHand:propsTypes.func,
        img:propsTypes.string,
        style:propsTypes.object
    }
    render(){
        let {children,img,clickHand,style}=this.props
        return(
            <div className="cell" onClick={(ev)=>clickHand(ev)}>
                <img className="cellImg" src={img} alt=""/>
                <div className="cellWord" style={style}>
                    <span>{children}</span>
                    <img src={arrow}/>
                </div>
            </div>
        )
    }
}