import React from 'react'
import './banner.css'
import $ from 'jquery'
import {withRouter} from 'react-router-dom'
import Swipe from './assests/js/swipe'
import propsTypes from "prop-types"
class Banner extends React.Component{
    propsTypes={
        bannerData:propsTypes.shape({
            _id:propsTypes.string,
            banner:propsTypes.string,
            title:propsTypes.string,
            sub_title:propsTypes.string
        }),
        to:propsTypes.shape({
            pathname:propsTypes.string,
            apiname:propsTypes.string
        })
    }
    to=(_id)=>{
        if(!this.props.to) return;
        let {history,to:{pathname,apiname}}=this.props;
        history.push({pathname:`${pathname}/${_id}`,search:`apiname=${apiname}`})
    }
    componentDidUpdate(){
        new Swipe($('.banner')[0],{
            auto:2000,
            continuous:true,
            stopPropation:true,
            callback:function (index,element){
              $('.banner ol li').removeClass('active');
              $('.banner ol li').eq(index).addClass('active');
            }
          })
    }
    render(){
        let {bannerData}=this.props
        return(
            <div className="banner">
                <ul className="clearfix">
                    {
                        bannerData.map(item=>(
                        <li key={item._id} onClick={()=>this.to(item._id)}>
                            <img src={item.image} alt=""/>
                            <div className="text-box">
                            <h2>{item.title}</h2>
                            <p>{item.sub_title}</p>
                            </div>
                        </li>
                        ))
                    }
                </ul>
            </div>
        )
    }
}
export default withRouter(Banner)