import React from 'react'
import {Link} from 'react-router-dom'
import '../assets/css/reg.css'
import logo from '../assets/img/logo.png'
import Input from '../component/input'
import Button from '../component/button'
export default class Reg extends React.Component{
    state={
        nikename:'',
        username:'',
        password:'',
        errorMess:""
    }
    changeIpt=(ev)=>{
        this.setState({
            [ev.target.name]:ev.target.value
        })
    }
    reg=async ()=>{
        // console.log("reg")
        let {username,password,nikename}=this.state;
        let formData=new FormData();
        formData.append("username",username);
        formData.append("password",password);
        formData.append("nikename",nikename);
        if(this.file.files.length>0){
            formData.append('icon',this.file.files[0])
        }
        let res = await axios({url:'/api/reg',method:'post',data:formData})
        if(res.data.err==0){
            this.props.history.push('/login')
        }else{
            this.setState({errorMess:res.data.msg})
        }
    }
    render(){
        let {nikename,username,password,errorMess} = this.state;
        return(
            <div className="reg">
                <img src={logo} alt="" onClick={()=>this.file.click()}/>
                <input type="file" ref={el=>this.file=el} style={{visibility:'hidden'}}/>
                <Input 
                label="设置昵称/头像" 
                style={{paddingLeft:".2rem",height:'.8rem',width:"60%","textAlign":"center"}} 
                model={{name:'nikename',value:nikename,onChange:(ev)=>this.changeIpt(ev)}}
                />
                <Input label="用户名"
                    style={{width:'80%'}}
                    model={{name:'username',value:username,onChange:(ev)=>this.changeIpt(ev)}}
                 />
                <Input 
                label="密码（6-20位）" 
                style={{width:'80%'}} 
                type="password" 
                model={{name:'password',value:password,onChange:(ev)=>this.changeIpt(ev)}}
                />
                {errorMess}
                <Button clickHandler={this.reg} style={{width:"70%",margin:".4rem auto 0",borderRadius:".1rem"}}>注册</Button>
                {/* <a className="goback">返回登录</a> */}
                <Link to="/login" className="goback">返回登录</Link>
                
            </div>
        )
    }
}