import React from 'react'
import './css/content.css'
import propsTypes from 'prop-types'
import {withRouter} from 'react-router-dom'
class Content extends React.Component{
    static defaultProps={
        clickHandler:()=>{},
        to:null
    }
    static propsTypes={
        clickHandler:propsTypes.func,
        data:propsTypes.shape({
            _id:propsTypes.string,
            image:propsTypes.string,
            des:propsTypes.string,
            time:propsTypes.string,
            num:propsTypes.number
        }).isRequired,
        to:propsTypes.shape({
            pathname:propsTypes.string,
            apiname:propsTypes.string
        })
    }
    componentDidMount(){
        // console.log("asnks",this.props)
    }
    to=(_id)=>{
        // console.log(this.props)
        if(!this.props.to) return;
        let {history,to:{pathname,apiname}} = this.props;
        history.push({pathname:`${pathname}/${_id}`,search:`apiname=${apiname}`})
    }
     render(){
        let {data:{_id,image,des,time,num},clickHandler}=this.props
        // console.log(this.props,'111')
        return(
            <div className="content" >
                <img src={image} alt="" onClick={()=>this.to(_id)}/>
                <p>{des}</p>
                {/* <p>在灰暗的日子中，不要让冷酷的命运窃喜；命运既然来凌辱我们，就应该用处之泰然的态度予以报复。--莎士比亚</p> */}
                <div className="share">
                    <a >{time}</a>
                    <div className="icon">
                        <span className="iconfont icon-ganxingqu" onClick={()=>clickHandler()}></span><i>{num}</i>
                        <span className="iconfont icon-shoucangicon"></span>
                        <span className="iconfont icon-fenxiang"></span>
                    </div>
                </div>
            </div>
        )
    }
}
export default withRouter(Content)