import React from 'react'
import {NavLink,withRouter} from 'react-router-dom'
import './nav.css'
class Nav extends React.Component{
    render(){
        return(
            <nav className="home_nav">
                <NavLink to="/home" activeClassName="home_color">早午茶</NavLink>
                <NavLink to="/home1" activeClassName="home_color">励志语录</NavLink>
                <NavLink to="/home2" activeClassName="home_color">爱情语录</NavLink>
                <NavLink to="/home3" activeClassName="home_color">人生感悟</NavLink>
                <NavLink to="/home4" activeClassName="home_color">伤感句子</NavLink>
                <NavLink to="/home5" activeClassName="home_color">名人名言</NavLink>
                <NavLink to="/home6" activeClassName="home_color">经典台词</NavLink>
                <NavLink to="/home7" activeClassName="home_color">英文语录</NavLink>
                <NavLink to="/home8" activeClassName="home_color">唯美句子</NavLink>
            </nav>
        )
    }
}
export default withRouter(Nav)