import React,{Component} from 'react'
import './Header.css'
import {withRouter} from 'react-router-dom'
import propsTypes from 'prop-types'
import arrow from '../assets/img/arrow1.png'
class Header extends Component{

    static propsTypes={
        topic:propsTypes.string,
        address:propsTypes.string,
        temp:propsTypes.string,
        left:propsTypes.string,
    }
    render(){
        let {topic,address,temp,left,history}=this.props;
        return(
            <div className="header">
                <div className="header_first" onClick={()=>history.go(-1)}>{left?<img src={arrow}/>:null}</div>
                <div className="header_second">{topic}</div>
                <div className="header_third">
                   <span>{address}</span>
                    <span>{temp}</span>
                </div>
            </div>
        )
    }
}
export default withRouter(Header)