import React from 'react'
import './aside.css'
import propsType from 'prop-types'
export default class Aside extends React.Component{
    static defaultProps={
        asideStyle:{}
    }
    propsType={
        asideStyle:propsType.object
    }
    render(){
        let {children,asideStyle}=this.props
        return(
            <div className="aside">
                <div style={asideStyle}></div>
                <span>{children}</span>
            </div>
           
        )
    }
}