import React from 'react'
import './css/input.css'
import propsTypes from 'prop-types'
export default class Input extends React.Component{
  /*
  * type   类型         text
  * model  双向绑定模式  null  |   {value,name,onChange}
  * multi  多行输入     false
  * label  标题         ''
  * style  行间样式     {}
  * */
 static defaltProps={
    type:'text',
    model:null,
    label:'',
    style:{},
    multi:false
 }
    static propsTypes={
        type:propsTypes.string,
        model:propsTypes.shape({
            value:propsTypes.string.isRequired,
            name:propsTypes.string.isRequired,
            onChange:propsTypes.func.isRequired
        }),
        multi:propsTypes.bool,
        style:propsTypes.object,
        label:propsTypes.string
        
    }
    renderInput=()=>{
        let {model,style,type,label} = this.props;
        let inputEle=null;
        //非受控元素
        if(!model){
            inputEle=(
                <div className="input">
                    <input type={type} placeholder={label} style={style}/>
                </div>
            )
            return inputEle;
        }
        //受控元素
        let {model:{value,name,onChange}}=this.props
        inputEle=(
            <div className="input">
                <input 
                type={type}
                placeholder={label} 
                style={style}
                value={value}
                name={name}
                onChange={onChange}
                style={style}
                />
            </div>
        )
        return inputEle

    } 
    renderTextarea=()=>{
        let {model,style,type,label} = this.props;
        let inputEle=null;
        //非受控元素
        if(!model){
            inputEle=(
                <div className="input">
                    <textarea type={type} placeholder={label} style={style}></textarea>
                </div>
            )
            return inputEle;
        }
        //受控元素
        let {model:{value,name,onChange}}=this.props
        inputEle=(
            <div className="input">
                {label}
                <textarea 
                value={value}
                name={name}
                onChange={onChange}
                style={style}
                ></textarea>
            </div>
        )
        return inputEle
    }
    render(){
        let {multi}=this.props
           return multi ?  this.renderTextarea():this.renderInput()
        }
}