import React from 'react'
import './psyTest.css'
import {withRouter} from "react-router-dom"
import propsTypes from 'prop-types'
class PsyTest extends React.Component{
    propsType={
        psyTestData:propsTypes.shape({
            _id:propsTypes.string,
            image:propsTypes.string,
            title:propsTypes.string,
            des:propsTypes.string
        }).isRequired,
        to:propsTypes.shape({
            apiname:propsTypes.string.isRequired,
            pathname:propsTypes.string.isRequired
        })
    }
    to=(_id)=>{
        if(!this.props.to) return;
        let {history,to:{pathname,apiname}}=this.props;
        history.push({pathname:`${pathname}/${_id}`,search:`apiname=${apiname}`})
    }
    render(){
        let {psyTestData:{_id,image,title,des}}=this.props
        return(
            <div className="psyTest" onClick={()=>this.to(_id)}>
                <h3>{title}</h3>
                <p>{des}</p>
                <img src={image}/>
            </div>
           
        )
    }
}
export default withRouter(PsyTest)