import React from 'react'
import {Link} from 'react-router-dom'
import '../assets/css/user.css'
import Header from '../layouts/Header'
import Cell from '../component/cell'
import logo from '../assets/img/logo.png'
import user1 from '../assets/img/user1.png'
import user2 from '../assets/img/user2.png'
import user3 from '../assets/img/user3.png'
import user4 from '../assets/img/user4.png'
import user5 from '../assets/img/user5.png'
import user6 from '../assets/img/user6.png'
import cell1 from '../assets/img/cell1.png'
import cell2 from '../assets/img/cell2.png'
import cell3 from '../assets/img/cell3.png'
import cell4 from '../assets/img/cell4.png'
import cell5 from '../assets/img/cell5.png'

import qs from 'qs'
export default class User extends React.Component{
    state={
        data:[]
    }
    click=()=>{
        console.log("cell")
    }
    logout=()=>{
        window.localStorage.removeItem("user")
        this.props.history.push('/login')
    }
    componentDidMount(){
        
        let res = window.localStorage.getItem("user")
        if(res){
            this.setState({data:qs.parse(res).data})
        }else{
            window.location.href=window.location.origin+'/login?path=user'
        }
    }
    render(){
        let {data} =this.state
        return(
            <div className="user">
               <Header topic="我的"/>
               <div className="userProfile">
                   <img src={this.baseUrl+data.icon} />
                   <div>
                    <Link to="/login">{data.nikename}</Link>
                    <Link to="/login" onClick={this.logout}>注销</Link>
                   </div>

                </div>
                <div className="userIcon">
                    <a><img src={user1}/><span>我的设置</span></a>
                    <a><img src={user2}/><span>我的关注</span></a>
                    <a><img src={user3}/><span>我的粉丝</span></a>
                    <a><img src={user4}/><span>我的消息</span></a>
                    <a><img src={user5}/><span>我的发布</span></a>
                    <a><img src={user6}/><span>我的收藏</span></a>
                </div>
                <div className="cellBox">
                    <Cell img={cell1} clickHand={this.click}>公众号与qq群</Cell>
                    <Cell img={cell2}>意见和建议</Cell>
                    <Cell img={cell3}>APP打分</Cell>
                    <Cell img={cell4}>版权申明</Cell>
                    <Cell img={cell5} style={{border:"none"}}>关于</Cell>
                </div>
            </div>
        )
    }
}