import React from 'react'
import {Link} from 'react-router-dom'
import qs from 'qs'
import '../assets/css/login.css'
import Input from '../component/input'
import Button from '../component/button'
import logo1 from '../assets/img/logo1.png'
import weichat from '../assets/img/weichat.png'
import qq from '../assets/img/qq.png'
export default class Login extends React.Component{
    state={
        nikename:'',
        username:'',
        password:'',
        errorMess:""
    }
    changeIpt=(ev)=>{
        this.setState({
            [ev.target.name]:ev.target.value
        })
    }
    login=async()=>{
        let {username,password,errorMess}=this.state;
        let parmas = new URLSearchParams()
        parmas.append("username",username)
        parmas.append("password",password)
        let res=await axios({
            url:"/api/login",
            method:'post',
            data:parmas
        })
        if(res.data.err==0){
            window.localStorage.setItem("user",qs.stringify(res.data))
            let path=qs.parse(this.props.location.search,{ignoreQueryPrefix:true}).path

            if(path&&!path.includes('/login')){
                this.props.history.push({
                    pathname:qs.parse(this.props.location.search,{ignoreQueryPrefix:true}).path
                })
            }else{
                this.props.history.push('/user')
            }
        }else{
            this.setState({errorMess:res.data.msg})
        }
    }
    render(){
        let {nikename,username,password,errorMess} = this.state;
        return(
            <div className="Login">
                <img className="loginImg" src={logo1} alt=""/>
                <Input label="用户名"
                    style={{width:'80%',marginTop:'.6rem'}}
                    model={{name:'username',value:username,onChange:(ev)=>this.changeIpt(ev)}}
                 />
                <Input 
                label="密码" 
                style={{width:'80%'}} 
                type="password" 
                model={{name:'password',value:password,onChange:(ev)=>this.changeIpt(ev)}}
                />
                {errorMess}
                <Button clickHandler={this.login} style={{width:"80%",margin:".4rem auto 0",borderRadius:".1rem"}}>登录</Button>
                {/* <a className="goback">返回登录</a> */}
                <Link to="/reg" className="goback">注册账号</Link>
                <div className="third">
                    <div></div>
                    <span>第三方账号快速登录</span>
                    <div></div>
                </div>
                <div className="methods">
                    <div className="weichat"><img src={weichat}/></div>
                    <div className="qq"><img src={qq}/></div>
                </div>
            </div>
        )
    }
}