import React from 'react'
import {NavLink,Route,Redirect,Switch} from 'react-router-dom'


import Footer from '../layouts/Footer'
import Home from '../pages/Home'
import Home1 from '../pages/Home1'
import Home2 from '../pages/Home2'
import Home3 from '../pages/Home3'
import Home4 from '../pages/Home4'
import Home5 from '../pages/Home5'
import Home6 from '../pages/Home6'
import Home7 from '../pages/Home7'
import Reg from '../pages/Reg'
import Login from '../pages/Login'
import User from '../pages/User'
import Psychological from '../pages/Psychological'
import Find from '../pages/Find'
import Details from '../pages/Details'
import NoPage from '../pages/NoPage'
import pubsub from 'pubsub-js'
import { Loading } from '../component/loading/loading'
export default class Default extends React.Component{
    state={
        bLoading:false,
        bFoot:false
    }
    constructor(){
        super()
        pubsub.subscribe('UPDATE_LOADING',(mess,data)=>{
            this.setState({bLoading:data})
        })
    }
    static getDerivedStateFromProps(nextProps,nextState){
        let path = nextProps.location.pathname;
        if(/home|home1|home2|home3|home4|home5|home6|home7|user|find|psychological/.test(path)){
            return{
                bFoot:true
            }
        }
        if(/login|reg|details/.test(path)){
            return{
                bFoot:false
            }
        }
    }  
    
    
    //   } 
    render(){
        let {bFoot,bLoading}=this.state
        return(
            <div className="default">
                {bLoading&&<Loading/>}
                <Switch>
                    <Route path="/home" component={Home}/>
                    <Route path="/home1" component={Home1}/>
                    <Route path="/home2" component={Home2}/>
                    <Route path="/home3" component={Home3}/>
                    <Route path="/home4" component={Home4}/>
                    <Route path="/home5" component={Home5}/>
                    <Route path="/home6" component={Home6}/>
                    <Route path="/home7" component={Home7}/>
                    <Route path="/reg" component={Reg}/>
                    <Route path="/login" component={Login}/>
                    <Route path="/user" component={User}/>
                    <Route path="/psychological" component={Psychological}/>
                    <Route path="/find" component={Find}/>
                    <Route path="/details/:_id" component={Details}/>
                    <Redirect exact path="/" to="/home"/>
                    <Route component={NoPage}/>
                </Switch>
                {bFoot? <Footer/>:null}
            </div>
        )
    }
}
