import React from 'react'
import Banner from '../component/banner'
import '../assets/css/find.css'
import find1 from '../assets/img/find1.png'
import find2 from '../assets/img/find2.png'
import find3 from '../assets/img/find3.png'
import find4 from '../assets/img/find4.png'
import FindContent from '../component/FindContent'
export default class Find extends React.Component{
    state={
        banner:[],
        home:[]
    }
    componentDidMount(){
        React.axios.all([
            React.axios({url:'/api/goods/banner',params:{_page:1,_limit:5}}),
            React.axios({url:'/api/goods/home',params:{_page:1,_limit:10}})
          ]).then(React.axios.spread((banner, home)=>{//banner|home ~~ res
            this.setState({
              banner:banner.data.data,
              home:home.data.data
            })
          }));
    }
    render(){
        let {banner,home}=this.state
        return(
            <div className="find">
                 <Banner
                        bannerData={banner}
                        to={{pathname:"/details",apiname:"banner"}}
                    />  
                <div className="findNav">
                    <a><img src={find1}/><span>远方</span></a>
                    <a><img src={find2}/><span>文章</span></a>
                    <a><img src={find3}/><span>三行情书</span></a>
                    <a><img src={find4}/><span>话题</span></a>
                </div>
                <div className="findList">
                    {
                        home.map(item=>(
                            <FindContent
                                key={item._id}
                                findData={item}
                             to={{pathname:"/details",apiname:"home"}}
                            />
                        ))
                    }
                    
                   
                </div>
            </div>
        )
    }
}