import React from 'react'
import {withRouter,NavLink} from 'react-router-dom'
// import qs from 'qs'
import '../assets/css/home.css'

import Header from '../layouts/Header'
import Content from '../component/content'
import Nav from '../component/nav/nav'
class Home7 extends React.Component{
    state={
        contentData:[]
    }
    async componentDidMount(){
        let res = await this.axios({url:'/api/goods/home',params:{_page:1,_limit:15}});
        this.setState({contentData:res.data.data})
    }
    add=()=>{
    }
    render(){
        let {contentData}=this.state
        return(
            <div className="home">
               <Header topic="下午茶" address="西安" temp="10℃ 阴"/>
               <Nav/>
               {
                   contentData.map(item=>{
                    return (
                        <Content 
                        key={item._id}
                        data={item}
                        to={{pathname:'/details',apiname:'home'}}
                        />
                    )
                   })
               }
            </div>
        )
    }
}
export default withRouter(Home7)