import React from 'react'
import Header from '../layouts/Header'
import '../assets/css/details.css'
import Button from '../component/button'
import zan from '../assets/img/zan.png';
import news from '../assets/img/new.png'
import qs from 'query-string'
import BareScreen from '../component/screen/screen';
export default class Details extends React.Component{
    state={
        data:{}
    }
    constructor(props){
        super(props)
        let apiname = qs.parse(props.location.search).apiname;
        let _id = props.match.params._id;
        this.axios({url:`/api/goods/${apiname}/${_id}`}).then(
        res=>this.setState({data:res.data.data})
        )
    }
 

    renderPage=({title,image,time,detail:{auth,auth_icon,content}})=>(
        <div className="details">
                <Header left/>
                <div className="detialProfile">
                    <div className="detailLeft">
                        <img src={auth_icon}/>
                        <div className="detailWord">
                            <h3>{auth}</h3>
                            <span>{time}</span>
                        </div>
                    </div>
                    <Button size="mini" style={{borderRadius:".1rem"}}>+ 关注</Button>
                </div>  
                <div className="detailContent">
                    <h3 className="dc_h3">{title}</h3>
                    <p className="dc_p">
                        <span>{content}</span>
                        <img src={image} alt=""/>
                    </p>
                    <div className="detailComment">
                        <h4>评论列表</h4>
                        <div className="dc_list">
                            <div className="dc_flex">
                                <div className="dcl_left">
                                    <img src="/images/icon1.png" alt=""/>
                                    <div className="dcl_content">
                                        <p>lala</p>
                                        <p>美文</p>
                                    </div>
                                </div>
                                <div className="dcl_right">{time}</div>
                            </div>
                            <div className="dc_bottom">
                                <a ><img src={zan}/><span>0</span></a>
                                <a ><img src={news}/><span>回复</span></a>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="detailFooter">
                    <input type="text" placeholder="写一句评论..."/>
                    <span className="iconfont icon-ganxingqu"><i>0</i></span>
                    <span className="iconfont icon-shoucangicon"></span>
                    <span className="iconfont icon-fenxiang"></span>
                </div>
            </div>
    )
    renderBareScreen=()=>(<BareScreen/>)
    render(){
        let el=null;

        if(this.state.data.title){
            el = this.renderPage(this.state.data)
        }else{
            el = this.renderBareScreen()
        }
        return el
    }
}