import React from 'react'
import './Footer.css'
import {NavLink} from 'react-router-dom'
export default class Footer extends React.Component{
    render (){
        return(
            <div className="footer">
                <NavLink
                    to="/home"
                >
                    <span className="iconfont icon-icon_huabanfuben"></span>
                    <p>首页</p>
                </NavLink>
                <NavLink
                    to="/find"
                >
                    <span className="iconfont icon-faxian"></span>
                    <p>发现</p>
                </NavLink>
                <NavLink
                    to="/lala"
                >
                   <span className="iconfont icon-jia"></span>
                </NavLink>
                <NavLink
                    to="/psychological"
                >
                   <span className="iconfont icon-xinxing"></span>
                    <p>心理</p>
                </NavLink>
                <NavLink
                    to="/user"
                >
                   <span className="iconfont icon-profile"></span>
                    <p>我的</p>
                </NavLink>
            </div>
        )
    }
}